export default {
  props: ['count'],
  data() {
    return {
      posts: null,
    }
  },
  async fetch() {
    try {
      this.posts = await this.$http.$get(`/api/articles/p/1/${this.count}`)
    } catch (err) {
      console.log(err)
    }
  },
}
